import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';

export interface CalendarStats {
  period: number;
  periodName: string;
  startDate: string;
  endDate: string;
  meetingsCount: number;
  tasksCount: number;
  visitsCount: number;
  travelsCount: number;
  totalEventsCount: number;
}

export interface CalendarStatsResponse {
  success: boolean;
  message: string;
  data: CalendarStats;
  errors: any[];
}

export enum CalendarPeriod {
  Day = 1,
  Week = 2,
  Month = 3,
}

@Injectable({
  providedIn: 'root',
})
export class CalendarStatsService {
  private apiUrl = `${environment.apiBaseUrl}/Calendar`;

  constructor(private http: HttpClient) {}

  getUserCalendarStats(
    period: CalendarPeriod
  ): Observable<CalendarStatsResponse> {
    return this.http.get<CalendarStatsResponse>(
      `${this.apiUrl}/user/stats?period=${period}`
    );
  }
}
